import React from 'react'
import Link from 'gatsby-link'
import Helmet from 'react-helmet'

import pic11 from '../assets/images/pic11.jpg'

const Generic = (props) => (
    <div>
        <Helmet>
            <title>Fleet Management Applications | Mike Weisert | mikew.design</title>
            <meta name="description" content="Defining " />
        </Helmet>

        <div id="main" className="alt">
            <section id="banner" className="major fleet">
                {/*  
                    <video playsinline autoplay muted loop id="homeBg" width="x" height="y">
                        <source src="static/Chicago.mp4" type="video/mp4" />
                    </video>
                */}
                    <div className="inner">
                    <div className="content">
                    
                        <h1>Designing a vehicle ordering system for over $2B in annual sales.</h1>
                                
                    </div>
                </div>
            </section>
            <section id="one">
                <div className="inner">
                    <div className="content">
                    
                    <div>Name withheld by client.</div>
                    
                    <h3>About the project</h3>
                    <ul>
                        <li>Leader in Fleet Management space</li>
                        <li>Processes $2B+ in vehicle sales</li>
                        <li>Multi-year project which began in 2015</li>
                    </ul>
       
                    <h3>The approach</h3>
                    - Develop an end to end UX process that would ensure client &amp; internal user needs
	- Agile team environment, important to run multiple workstreams to stay ahead of development
	- Work to keep re-work to a minimum
	- Process is:
		○ User discovery
		○ Sketching / workshops
		○ Wireframes &amp; prototypes
		○ SME validation &amp; testing
		○ Unmoderated testing approaches
		○ Build
		○ SME validation &amp; testing
	- Traveled to the organization's client sites across the US.  Based on findings, created product recommendations; many of which are currently under development.

            
                <h3>Key Successes</h3>
	- Grew the UX team from a single individual to leading multiple UX team members on multiple workstreams
	- Helped define a shared style guide for the organization
	- Created methodology for unmoderrated testing to ensure usability prior to team development
	- Aligned team to key deliverables and vision by prototyping long term path
		○ Single source of truth
	- Helped define the long term vision to leadership to secure ongoing funding for the project ($20m+)
	- Recognized with 'exceptional' review for contractor by the organization
	
	- Presenter at organization's Account Management summit regarding client insights
            
         
                    <h3>What's Next?</h3>
	- Based on success of the ordering project, I've also been involved in designing new products for the org.
		○ Proactive fleet management - a system for telling clients what they should be focused on and guiding them through the complexity of tasks
                
                </div>
                </div>
            
            </section>

            <section id="two">
                <div className="inner">
                <Link to="/ci" className="button big">Next: Children International</Link>
                </div>        
            </section>
        </div>

    </div>
)

export default Generic


